import React from "react";
import { BannerHome } from "../components/BannerHome";
import { CardProduct } from "../components/CardProduct";
import { useFetch } from "../hooks/useFetch";
import {toThousand} from '../helpers'

export const Home = () => {
    const apiURLBase = process.env.REACT_APP_API_URL_BASE

    const {loading: loadNewest, data:newest } = useFetch(apiURLBase + '/products?order=DESC&sortBy=newest&limit=4');
    const {loading: loadSales, data:sales} = useFetch(apiURLBase + '/products?sales=1&limit=4&sortBy=name&order=DESC');

  return (
    <>
      <BannerHome />
      <div className="container products-wrapper">
        <div className="row">
          <div className="col-12">
            <h2 className="products-title">Últimos agregados</h2>
          </div>
          {
            loadNewest 
            ?
            (
                <div className="alert alert-info">
                    Cargando....
                </div>
            )
            :
            (
                newest.map((product) => (
                    <CardProduct {...product} toThousand = {toThousand}/>
                ))
            )
          }
        </div>
      </div>
      <div className="container products-wrapper">
        <div className="row">
          <div className="col-12">
            <h2 className="products-title">Ofertas</h2>
          </div>
          {
            loadSales 
            ?
            (
                <div className="alert alert-info">
                    Cargando....
                </div>
            )
            :
            (
                sales.map((product) => (
                    <CardProduct {...product} toThousand = {toThousand}/>
                ))
            )
          }
        </div>
      </div>
   
    </>
  );
};
