import { useEffect, useState } from 'react'

export const useFetch = (url) => {
    
    const [state, setState] = useState({
        loading : true,
         error : null,
         data : null
    });

    useEffect(() => {

        const fetchData = async () => {
            let response = await fetch(url);
            let result = await response.json();
            console.log(result)
   
            setState({
                loading : false,
                error : result.error,
                data : result.data
            })

        }

        fetchData()
       
    }, [url]);

    return state
}
